  fill: #5ae8a4;
  width: 30px;
  height: 30px;
  transform: rotate(-45deg);
  transform-origin: center;
  transition: transform 0.3s ease-in-out;
}
body.not-homepage .faqs .accordion button.active svg {
  transform: rotate(0deg);
  transform-origin: center;
  transition: transform 0.3s ease-in-out;
}
body.not-homepage .faqs .accordion .answer {
